@header-height: 50px;
@border: 10px;
@panel-margin-x: 5vw;
@panel-margin-y: 5vh;

.app {
    width: 100vw;
    height: 100vh;
    font-weight: 200;
    &-head {
        height: @header-height;
        text-align: center;
        line-height: @header-height;
    }
    &-panel {
        position: fixed;
        top: @panel-margin-y;
        bottom: @panel-margin-y;
        left: @panel-margin-x;
        right: @panel-margin-x;
        background: #fff;
        border-radius: @border;
        z-index: 101;
        &-mask {
            position: fixed;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            padding: 5vh 5vw;
            background: rgba(0, 0, 0, 0.8);
            z-index: 100;
        }
        &-head {
            position: relative;
            padding: 10px 0;
            border-bottom: 1px solid #eceff1;
            text-align: center;
            span {
                position: absolute;
                right: 20px;
                float: right;
                font-weight: bold;
                color: #ccc;
                cursor: pointer;
                transition: all 0.3s ease-in-out;
                &:hover {
                    color: #000;
                }
            }
        }
        &-iframe {
            width: 100%;
            height: 100%;
        }
    }
    &-content {
        position: fixed;
        top: @header-height;
        bottom: 0;
        left: 0;
        right: 0;
        background: #eceff1;
        &-item {
            display: inline-block;
            margin: 20px;
            width: 30vw;
            border-radius: @border;
            background: white;
            transition: all 0.5s ease-in-out;
            cursor: pointer;
            &:hover {
                box-shadow: 0 0 20px #ccc;
            }

            &-img {
                width: 100%;
                border-radius: @border @border 0 0;
            }
            &-footer {
                padding: 10px 20px;
                text-align: center;
            }
        }
    }
}
